import {
  REGISTRO_EXITOSO,
  REGISTRO_ERROR,
  OBTENER_USUARIO,
  LOGIN_EXITOSO,
  LOGIN_ERROR,
  CERRAR_SESION,
} from "../../types";
import axios from "axios";

export default async (state, action) => {
  switch (action.type) {
    case REGISTRO_EXITOSO:
      let objData = {
        email: action.payloadEmail,
        nombre: action.payloadNombre,
        id: action.payloadId,
      };

      await axios
        .post("/correo", objData)
        .then((res) => console.log(res))
        .catch((err) => console.log(err));
      return {
        ...state,
        // autenticado: true,
        mensaje: null,
        // cargando: false,
      };

    case LOGIN_EXITOSO:
      localStorage.setItem("token", action.payload.token);

      return {
        ...state,
        autenticado: true,
        mensaje: null,
        cargando: false,
      };

    case OBTENER_USUARIO:
      return {
        ...state,
        autenticado: true,
        usuario: action.payload,
        cargando: false,
      };
    case CERRAR_SESION:
    case LOGIN_ERROR:
    case REGISTRO_ERROR:
      localStorage.removeItem("token");
      return {
        ...state,
        token: null,
        usuario: null,
        autenticado: null,
        mensaje: action.payload,
        cargando: false,
      };

    default:
      return state;
  }
};
